.contact-hero {
    width: 100%;
    height: 70vh;
    position: relative;
    background: rgba(0, 0, 0, .4);
}

.contact-hero:before {
    content: '';
    position: absolute;
    background: url(/sacramento-rehab/static/media/contact-bg.2c4edd39.jpg)no-repeat center center/cover;
    height: 70vh;
    width: 100%;
    z-index: -1;
}

.contact-hero .container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
}

.contact-hero h1 {
    color: var(--primary-color);
    font-size: 5rem;
}

.contact-hero .content {
